<template>
  <section class="center-special-news">
    <div class="container mx-auto p-1 sm:mx-1 max-w-screen-md md:mb-3">
      <div class="w-full h-auto">
          <p class="text-black font-bold text-start">
            اخبار ویژه
          </p>
        <div class="bg-slate-600 w-full h-0.5 my-2"></div>
        <div class="flex flex-col space-y-2">
          <div v-for="value in box" :key="value" class="grid grid-cols-3 grid-rows-2 gap-4" style="height:300px">
            <div v-for="items in value" :key="items" class="col-span-1 row-span-1">
              <div>
                <figure>
                  <a href="#">
                <v-lazy-image @error="notShow" :src="`${items.data.data.details.news_image[0].url}`" alt="" class="transition-all duration-300 hover:scale-110 cursor-pointer  w-full h-24"/>
                  </a>
                </figure>
              </div>
              <p id="message" class="text-black font-thin" style="font-size: 11px">{{items.data.data.details.title}}</p>
              <div class="flex flex-row align-center justify-between mt-2.5">
                <div><p class="text-slate-700 font-thin" style="font-size: 10px">9 ساعت قبل</p></div>
                <div class="flex flex-row">
                  <font-awesome-icon :icon="['fas','comment']" class="text-slate-600" style="font-size: 10px"></font-awesome-icon>
                  <span class="text-slate-600 font-thin" style="font-size: 10px">{{items.data.data.details.news_comment_count}}</span>
                  <font-awesome-icon :icon="['fas','eye']" class="text-slate-600" style="font-size: 10px"></font-awesome-icon>
                  <span class="text-slate-600 font-thin" style="font-size: 10px">{{items.data.data.details.visit}}</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import{FontAwesomeIcon} from "@fortawesome/vue-fontawesome";
import VLazyImage from "v-lazy-image";
import{ref} from 'vue'
export default {
  name: "SpiNews",
  components:{FontAwesomeIcon,VLazyImage},
  props:{
    box:{
      type:Array,
      required:true,
      default:null
    }
  },
  setup(){
    const notFound=ref('https://footballi.net/assets/shared-application/images/placeholders/ic_default_news_image.png')
    function notShow(event){
      event.target.src=notFound.value
    }
    return{
      notFound,notShow

    }
  }
}
</script>

<style scoped>
figure{
  overflow: hidden;
}
</style>